import { debounce } from 'lodash';

import React, { Component } from 'react';
import { PanelBody} from '@wordpress/components';

class PeekAttributes extends Component {
	state = {
		block: undefined,
	};

	componentDidMount() {
		const update = ( e ) => {
			const el = document.elementFromPoint( e.pageX, e.pageY );
			const attr = el.getAttribute( 'data-block' );

			if ( attr ) {
				const block = wp.data
					.select( 'core/block-editor' )
					.getBlocksByClientId( attr )[ 0 ];
				this.setState( { block: block } );
			} else {
				this.setState( { block: undefined } );
			}
		};

		const debouncedMM = debounce( update, 300 );

		document.addEventListener( 'mousemove', debouncedMM );
	}
	render() {



		return (
			<PanelBody>
				{ this.state.block && Object.keys(this.state.block).map( key => <li key={key}><span>{key}:</span><span>{this.state.block[key]}</span></li>) }
			</PanelBody>
		);
	}
}

export default PeekAttributes;
